.configuration-page {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;

  .page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;

    h1 {
      margin: 0;
      color: #2c3e50;
    }

    .header-actions {
      display: flex;
      gap: 1rem;
    }
  }

  .config-info {
    background-color: #ecf0f1;
    padding: 1rem;
    border-radius: 4px;
    margin-bottom: 2rem;
    display: flex;
    gap: 2rem;

    span {
      color: #34495e;
    }
  }

  .config-section {
    background: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-bottom: 1rem;
    overflow: hidden;
    transition: all 0.3s ease;

    &.expanded {
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    }

    .section-header {
      padding: 1rem 1.5rem;
      background-color: #34495e;
      color: white;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: background-color 0.3s;

      &:hover {
        background-color: #2c3e50;
      }

      .section-title {
        font-size: 1.1rem;
        font-weight: 600;
      }

      .section-toggle {
        font-size: 1.2rem;
      }
    }

    .section-content {
      padding: 1.5rem;

      .section-actions {
        display: flex;
        justify-content: flex-end;
        gap: 0.5rem;
        margin-bottom: 1rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid #ecf0f1;
      }

      .form-grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 1.5rem;

        .form-field {
          display: flex;
          flex-direction: column;

          &.full-width {
            grid-column: 1 / -1;
          }

          label {
            font-weight: 600;
            margin-bottom: 0.5rem;
            color: #2c3e50;
          }

          input[type="text"],
          input[type="password"],
          input[type="number"],
          select,
          textarea {
            padding: 0.5rem;
            border: 1px solid #ddd;
            border-radius: 4px;
            font-size: 1rem;

            &:disabled {
              background-color: #ecf0f1;
              cursor: not-allowed;
            }

            &:focus {
              outline: none;
              border-color: #3498db;
            }
          }

          input[type="checkbox"] {
            width: 20px;
            height: 20px;
            cursor: pointer;

            &:disabled {
              cursor: not-allowed;
              opacity: 0.5;
            }
          }
        }
      }

      .table-container {
        overflow-x: auto;
        margin-bottom: 2rem;

        .data-table {
          width: 100%;
          border-collapse: collapse;

          th, td {
            padding: 0.75rem;
            text-align: left;
            border-bottom: 1px solid #ecf0f1;
          }

          th {
            background-color: #ecf0f1;
            font-weight: 600;
            color: #2c3e50;
          }

          tr {
            &:hover {
              background-color: #f8f9fa;
            }

            &.disabled {
              opacity: 0.6;
              background-color: #fafafa;
            }
          }

          td.wrap {
            max-width: 300px;
            word-wrap: break-word;
          }
        }
      }

      .add-form {
        background-color: #f8f9fa;
        padding: 1.5rem;
        border-radius: 8px;
        margin-top: 2rem;

        h3 {
          margin-top: 0;
          margin-bottom: 1rem;
          color: #2c3e50;
        }
      }
    }
  }

  .loading {
    text-align: center;
    padding: 4rem;
    font-size: 1.2rem;
    color: #7f8c8d;
  }
}

// Buttons
.btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s;

  &.btn-primary {
    background-color: #3498db;
    color: white;

    &:hover:not(:disabled) {
      background-color: #2980b9;
    }
  }

  &.btn-secondary {
    background-color: #95a5a6;
    color: white;

    &:hover:not(:disabled) {
      background-color: #7f8c8d;
    }
  }

  &.btn-danger {
    background-color: #e74c3c;
    color: white;

    &:hover:not(:disabled) {
      background-color: #c0392b;
    }
  }

  &.btn-sm {
    padding: 0.25rem 0.75rem;
    font-size: 0.875rem;
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

// Modals
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;

  .modal {
    background: white;
    border-radius: 8px;
    max-width: 800px;
    width: 90%;
    max-height: 90vh;
    display: flex;
    flex-direction: column;

    .modal-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1.5rem;
      border-bottom: 1px solid #ecf0f1;

      h2 {
        margin: 0;
        color: #2c3e50;
      }

      .modal-close {
        background: none;
        border: none;
        font-size: 2rem;
        cursor: pointer;
        color: #7f8c8d;
        padding: 0;
        width: 2rem;
        height: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
          color: #2c3e50;
        }
      }
    }

    .modal-body {
      padding: 1.5rem;
      overflow-y: auto;

      textarea {
        width: 100%;
        min-height: 400px;
        font-family: 'Courier New', monospace;
        font-size: 0.875rem;
        padding: 1rem;
        border: 1px solid #ddd;
        border-radius: 4px;
        resize: vertical;
      }

      .error-message {
        color: #e74c3c;
        padding: 0.75rem;
        background-color: #fadbd8;
        border-radius: 4px;
        margin-top: 1rem;
      }

      .modal-actions {
        display: flex;
        justify-content: flex-end;
        gap: 1rem;
        margin-top: 1.5rem;
      }
    }
  }
}
